/* ================================================
   ULTIMATE ADMIN DASHBOARD - PREMIUM EDITION
   ================================================ */

/* === CSS Variables === */
:root {
    --dash-primary: #4F46E5;
    --dash-secondary: #7C3AED;
    --dash-accent: #EC4899;
    --dash-success: #00ff88;
    --dash-warning: #ffd43b;
    --dash-danger: #ff6b6b;
    --dash-info: #4dabf7;
    --dash-glass: rgba(255, 255, 255, 0.05);
    --dash-glass-border: rgba(255, 255, 255, 0.1);
    --dash-glass-hover: rgba(255, 255, 255, 0.1);
  }
  
  /* === Animations === */
  @keyframes fadeInUp {
    from {
      opacity: 0;
      transform: translateY(30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
  }
  
  @keyframes scaleIn {
    from {
      opacity: 0;
      transform: scale(0.9);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }
  
  @keyframes slideInLeft {
    from {
      opacity: 0;
      transform: translateX(-30px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  
  @keyframes pulse {
    0%, 100% {
      opacity: 1;
      transform: scale(1);
    }
    50% {
      opacity: 0.8;
      transform: scale(1.05);
    }
  }
  
  @keyframes float {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-10px); }
  }
  
  @keyframes shimmer {
    0% { background-position: -200% center; }
    100% { background-position: 200% center; }
  }
  
  @keyframes gradientMove {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
  }
  
  @keyframes ripple {
    0% {
      transform: scale(1);
      opacity: 0.4;
    }
    100% {
      transform: scale(2.5);
      opacity: 0;
    }
  }
  
  @keyframes glow {
    0%, 100% { 
      box-shadow: 0 0 20px rgba(79, 70, 229, 0.3);
    }
    50% { 
      box-shadow: 0 0 40px rgba(79, 70, 229, 0.6);
    }
  }
  
  @keyframes blink {
    0%, 100% { opacity: 1; }
    50% { opacity: 0; }
  }
  
  @keyframes rotate {
    from { transform: rotate(0deg); }
    to { transform: rotate(360deg); }
  }
  
  @keyframes drawLine {
    from { stroke-dashoffset: 1000; }
    to { stroke-dashoffset: 0; }
  }
  
  @keyframes particleFloat {
    0%, 100% {
      transform: translate(0, 0) rotate(0deg);
      opacity: 0;
    }
    10% { opacity: 1; }
    90% { opacity: 1; }
    100% {
      transform: translate(var(--x), -100vh) rotate(360deg);
      opacity: 0;
    }
  }
  
  @keyframes progressBar {
    0% { width: 0%; }
    100% { width: 100%; }
  }
  
  @keyframes loaderRotate {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }
  
  @keyframes loaderPulse {
    0%, 100% { 
      transform: scale(0.8);
      opacity: 0.5;
    }
    50% { 
      transform: scale(1);
      opacity: 1;
    }
  }
  
  /* === Main Container === */
  .admin-dashboard-premium {
    min-height: 100vh;
    padding: 30px;
    position: relative;
    overflow-x: hidden;
  }
  
  /* === Background Effects === */
  .dashboard-bg {
    position: fixed;
    inset: 0;
    pointer-events: none;
    z-index: 0;
  }
  
  .bg-gradient {
    position: absolute;
    inset: 0;
    background: 
      radial-gradient(ellipse at 20% 20%, rgba(79, 70, 229, 0.15) 0%, transparent 50%),
      radial-gradient(ellipse at 80% 80%, rgba(236, 72, 153, 0.1) 0%, transparent 50%),
      radial-gradient(ellipse at 50% 50%, rgba(124, 58, 237, 0.05) 0%, transparent 70%);
    animation: gradientMove 15s ease-in-out infinite;
    background-size: 200% 200%;
  }
  
  .bg-grid {
    position: absolute;
    inset: 0;
    background-image: 
      linear-gradient(rgba(255, 255, 255, 0.02) 1px, transparent 1px),
      linear-gradient(90deg, rgba(255, 255, 255, 0.02) 1px, transparent 1px);
    background-size: 50px 50px;
    mask-image: radial-gradient(ellipse at center, black 30%, transparent 80%);
  }
  
  .bg-particles .particle {
    position: absolute;
    width: 4px;
    height: 4px;
    background: rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    left: var(--x);
    top: 100%;
    animation: particleFloat var(--duration) var(--delay) infinite linear;
  }
  
  /* === Header === */
  .dashboard-header {
    position: relative;
    z-index: 10;
    margin-bottom: 30px;
    animation: fadeInUp 0.6s ease;
  }
  
  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px 30px;
    background: var(--dash-glass);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 24px;
    border: 1px solid var(--dash-glass-border);
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .header-left {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  
  .header-icon {
    width: 60px;
    height: 60px;
    background: linear-gradient(135deg, var(--dash-primary), var(--dash-secondary));
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 10px 30px rgba(79, 70, 229, 0.3);
    animation: float 3s ease-in-out infinite;
  }
  
  .header-icon svg {
    width: 30px;
    height: 30px;
    color: #fff;
  }
  
  .header-text h1 {
    font-size: 2rem;
    font-weight: 800;
    margin: 0 0 5px 0;
    background: linear-gradient(135deg, #fff 0%, rgba(255,255,255,0.7) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .header-text p {
    margin: 0;
    font-size: 0.95rem;
    opacity: 0.7;
  }
  
  .header-right {
    display: flex;
    align-items: center;
    gap: 20px;
  }
  
  .header-stats {
    display: flex;
    gap: 20px;
  }
  
  .header-stat {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 16px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 12px;
    font-size: 0.9rem;
  }
  
  .online-dot {
    width: 8px;
    height: 8px;
    background: var(--dash-success);
    border-radius: 50%;
    animation: pulse 2s infinite;
    box-shadow: 0 0 10px var(--dash-success);
  }
  
  .update-icon {
    animation: rotate 2s linear infinite;
    display: inline-block;
  }
  
  .export-btn-premium {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 14px 24px;
    background: linear-gradient(135deg, var(--dash-primary) 0%, var(--dash-secondary) 100%);
    color: #fff;
    border: none;
    border-radius: 14px;
    font-size: 0.95rem;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
    box-shadow: 0 10px 30px rgba(79, 70, 229, 0.3);
    position: relative;
    overflow: hidden;
  }
  
  .export-btn-premium::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
    transition: left 0.5s;
  }
  
  .export-btn-premium:hover {
    transform: translateY(-3px);
    box-shadow: 0 15px 40px rgba(79, 70, 229, 0.4);
  }
  
  .export-btn-premium:hover::before {
    left: 100%;
  }
  
  .export-btn-premium svg {
    width: 20px;
    height: 20px;
  }
  
  /* === Stats Section === */
  .stats-section {
    position: relative;
    z-index: 10;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    gap: 24px;
    margin-bottom: 30px;
  }
  
  .stat-card-premium {
    position: relative;
    padding: 24px;
    background: var(--dash-glass);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 20px;
    border: 1px solid var(--dash-glass-border);
    overflow: hidden;
    transition: all 0.4s cubic-bezier(0.25, 0.8, 0.25, 1);
    animation: fadeInUp 0.6s ease both;
    animation-delay: var(--delay);
  }
  
  .stat-card-premium:hover {
    transform: translateY(-8px) scale(1.02);
    border-color: var(--accent-color);
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.3);
  }
  
  .stat-card-bg {
    position: absolute;
    inset: 0;
    background: linear-gradient(135deg, 
      rgba(var(--accent-color), 0.1) 0%, 
      transparent 50%);
    opacity: 0;
    transition: opacity 0.3s;
  }
  
  .stat-card-premium:hover .stat-card-bg {
    opacity: 1;
  }
  
  .stat-card-glow {
    position: absolute;
    top: -50%;
    right: -50%;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle, var(--accent-color) 0%, transparent 70%);
    opacity: 0;
    filter: blur(60px);
    transition: opacity 0.3s;
    pointer-events: none;
  }
  
  .stat-card-premium:hover .stat-card-glow {
    opacity: 0.2;
  }
  
  .stat-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 16px;
  }
  
  .stat-icon-wrapper {
    width: 50px;
    height: 50px;
    border-radius: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s;
  }
  
  .stat-icon-wrapper svg {
    width: 26px;
    height: 26px;
    color: var(--accent-color);
  }
  
  .stat-card-premium:hover .stat-icon-wrapper {
    transform: scale(1.1) rotate(5deg);
  }
  
  .stat-trend {
    display: flex;
    align-items: center;
    gap: 4px;
    padding: 6px 10px;
    border-radius: 20px;
    font-size: 0.8rem;
    font-weight: 600;
  }
  
  .stat-trend.up {
    background: rgba(0, 255, 136, 0.15);
    color: var(--dash-success);
  }
  
  .stat-trend.down {
    background: rgba(255, 107, 107, 0.15);
    color: var(--dash-danger);
  }
  
  .trend-icon {
    font-size: 1rem;
  }
  
  .stat-body {
    position: relative;
    z-index: 1;
  }
  
  .stat-value {
    font-size: 2.5rem;
    font-weight: 800;
    line-height: 1;
    margin-bottom: 8px;
    background: linear-gradient(135deg, #fff 0%, rgba(255,255,255,0.8) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .stat-label {
    font-size: 0.9rem;
    text-transform: uppercase;
    letter-spacing: 1px;
    opacity: 0.6;
    font-weight: 600;
  }
  
  .stat-sparkline {
    margin-top: 16px;
    height: 40px;
    opacity: 0.8;
  }
  
  .sparkline-chart {
    width: 100%;
    height: 100%;
  }
  
  .sparkline-area {
    animation: fadeIn 1s ease;
  }
  
  .sparkline-line {
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: drawLine 2s ease forwards;
  }
  
  .sparkline-dot {
    animation: pulse 2s infinite;
  }
  
  /* === Charts Section === */
  .charts-section {
    position: relative;
    z-index: 10;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr;
    gap: 24px;
    margin-bottom: 30px;
  }
  
  .charts-section.secondary {
    grid-template-columns: 1.5fr 1fr 1fr;
  }
  
  .chart-card {
    background: var(--dash-glass);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 20px;
    border: 1px solid var(--dash-glass-border);
    overflow: hidden;
    animation: fadeInUp 0.6s ease both;
    animation-delay: 0.2s;
  }
  
  .chart-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 24px;
    border-bottom: 1px solid var(--dash-glass-border);
  }
  
  .chart-header h3 {
    display: flex;
    align-items: center;
    gap: 12px;
    margin: 0;
    font-size: 1rem;
    font-weight: 600;
    opacity: 0.9;
  }
  
  .chart-header h3 svg {
    width: 20px;
    height: 20px;
    opacity: 0.7;
  }
  
  .chart-tabs {
    display: flex;
    gap: 8px;
  }
  
  .chart-tab {
    padding: 8px 16px;
    background: transparent;
    border: 1px solid var(--dash-glass-border);
    border-radius: 8px;
    color: inherit;
    font-size: 0.85rem;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .chart-tab:hover {
    background: var(--dash-glass-hover);
  }
  
  .chart-tab.active {
    background: var(--dash-primary);
    border-color: var(--dash-primary);
    color: #fff;
  }
  
  .chart-body {
    padding: 24px;
  }
  
  .donut-body {
    display: flex;
    justify-content: center;
  }
  
  /* === Bar Chart === */
  .bar-chart {
    position: relative;
    display: flex;
  }
  
  .bar-chart-grid {
    position: absolute;
    inset: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-right: 40px;
  }
  
  .grid-line {
    border-bottom: 1px dashed rgba(255, 255, 255, 0.1);
    position: relative;
  }
  
  .grid-label {
    position: absolute;
    right: -35px;
    top: -8px;
    font-size: 0.7rem;
    opacity: 0.5;
  }
  
  .bar-chart-bars {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    gap: 16px;
    width: 100%;
    height: 100%;
    padding-right: 40px;
  }
  
  .bar-wrapper {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
  }
  
  .bar {
    width: 100%;
    height: var(--bar-height);
    background: linear-gradient(180deg, var(--bar-color) 0%, rgba(79, 70, 229, 0.3) 100%);
    border-radius: 8px 8px 0 0;
    position: relative;
    transition: all 0.3s;
    animation: scaleIn 0.5s ease both;
    animation-delay: var(--bar-delay);
    transform-origin: bottom;
  }
  
  .bar:hover {
    filter: brightness(1.2);
    box-shadow: 0 0 20px var(--bar-color);
  }
  
  .bar-value {
    position: absolute;
    top: -25px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 0.8rem;
    font-weight: 600;
    opacity: 0;
    transition: opacity 0.2s;
  }
  
  .bar:hover .bar-value {
    opacity: 1;
  }
  
  .bar-label {
    margin-top: 10px;
    font-size: 0.75rem;
    opacity: 0.6;
  }
  
  /* === Donut Chart === */
  .donut-chart-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
  
  .donut-chart {
    width: 160px;
    height: 160px;
  }
  
  .donut-segment {
    fill: none;
    stroke-linecap: round;
    animation: scaleIn 0.8s ease both;
    animation-delay: var(--delay, 0s);
    transition: all 0.3s;
  }
  
  .donut-segment:hover {
    filter: brightness(1.2);
    stroke-width: 14;
  }
  
  .donut-total {
    fill: #fff;
    font-size: 1.2rem;
    font-weight: 800;
  }
  
  .donut-label {
    fill: rgba(255, 255, 255, 0.6);
    font-size: 0.35rem;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .donut-legend {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
  }
  
  .legend-item {
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 0.85rem;
  }
  
  .legend-color {
    width: 12px;
    height: 12px;
    border-radius: 4px;
  }
  
  .legend-label {
    flex: 1;
    opacity: 0.8;
  }
  
  .legend-value {
    font-weight: 600;
  }
  
  /* === System Log (Terminal) === */
  .terminal-card {
    padding: 0;
  }
  
  .system-log {
    height: 100%;
    display: flex;
    flex-direction: column;
    font-family: 'JetBrains Mono', 'Fira Code', 'Consolas', monospace;
    background: rgba(0, 0, 0, 0.4);
  }
  
  .log-header {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 14px 20px;
    background: rgba(255, 255, 255, 0.05);
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
  
  .terminal-dots {
    display: flex;
    gap: 6px;
  }
  
  .terminal-dots .dot {
    width: 12px;
    height: 12px;
    border-radius: 50%;
  }
  
  .terminal-dots .dot.red { background: #ff5f56; }
  .terminal-dots .dot.yellow { background: #ffbd2e; }
  .terminal-dots .dot.green { background: #27ca40; }
  
  .terminal-title {
    font-size: 0.8rem;
    opacity: 0.6;
  }
  
  .log-content {
    flex: 1;
    padding: 16px;
    overflow-y: auto;
    max-height: 280px;
  }
  
  .log-entry {
    display: flex;
    align-items: flex-start;
    gap: 10px;
    margin-bottom: 10px;
    font-size: 0.8rem;
    animation: fadeIn 0.3s ease both;
    animation-delay: var(--delay);
  }
  
  .log-time {
    color: rgba(255, 255, 255, 0.4);
    white-space: nowrap;
  }
  
  .log-type {
    padding: 2px 8px;
    border-radius: 4px;
    font-size: 0.7rem;
    font-weight: 600;
    text-transform: uppercase;
  }
  
  .log-type.info { 
    background: rgba(77, 171, 247, 0.2); 
    color: var(--dash-info); 
  }
  .log-type.success { 
    background: rgba(0, 255, 136, 0.2); 
    color: var(--dash-success); 
  }
  .log-type.warning { 
    background: rgba(255, 212, 59, 0.2); 
    color: var(--dash-warning); 
  }
  .log-type.debug { 
    background: rgba(156, 163, 175, 0.2); 
    color: #9ca3af; 
  }
  
  .log-message {
    color: rgba(255, 255, 255, 0.9);
    flex: 1;
  }
  
  .log-cursor {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-top: 10px;
  }
  
  .cursor-prompt {
    color: var(--dash-success);
  }
  
  .cursor-blink {
    color: var(--dash-success);
    animation: blink 1s infinite;
  }
  
  /* === Live Activity Feed === */
  .feed-card {
    padding: 0;
  }
  
  .live-feed {
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  
  .feed-header {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px 20px;
    background: rgba(255, 255, 255, 0.05);
    border-bottom: 1px solid var(--dash-glass-border);
  }
  
  .live-indicator {
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 4px 10px;
    background: rgba(255, 107, 107, 0.2);
    border-radius: 20px;
    font-size: 0.7rem;
    font-weight: 700;
    color: var(--dash-danger);
  }
  
  .pulse-dot {
    width: 8px;
    height: 8px;
    background: var(--dash-danger);
    border-radius: 50%;
    animation: pulse 1s infinite;
  }
  
  .feed-title {
    font-weight: 600;
    font-size: 0.95rem;
  }
  
  .feed-content {
    flex: 1;
    padding: 16px;
    overflow-y: auto;
    max-height: 280px;
  }
  
  .feed-item {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 12px;
    margin-bottom: 8px;
    background: rgba(255, 255, 255, 0.03);
    border-radius: 12px;
    transition: all 0.2s;
    animation: slideInLeft 0.3s ease both;
    animation-delay: var(--delay);
  }
  
  .feed-item:hover {
    background: rgba(255, 255, 255, 0.08);
    transform: translateX(5px);
  }
  
  .feed-icon {
    font-size: 1.2rem;
  }
  
  .feed-details {
    flex: 1;
    display: flex;
    flex-direction: column;
  }
  
  .feed-action {
    font-weight: 600;
    font-size: 0.85rem;
    text-transform: capitalize;
  }
  
  .feed-page {
    font-size: 0.75rem;
    opacity: 0.6;
  }
  
  .feed-time {
    font-size: 0.75rem;
    opacity: 0.5;
  }
  
  /* === World Map === */
  .world-map {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .map-svg {
    width: 100%;
    height: 150px;
  }
  
  .map-outline {
    animation: drawLine 2s ease forwards;
    stroke-dasharray: 1000;
    stroke-dashoffset: 1000;
  }
  
  .map-point .point-ripple {
    fill: var(--dash-primary);
    opacity: 0;
    animation: ripple 2s infinite;
    animation-delay: var(--delay);
  }
  
  .map-point .point-dot {
    fill: #fff;
    animation: scaleIn 0.5s ease both;
    animation-delay: var(--delay);
  }
  
  .map-point .point-label {
    fill: #fff;
    font-size: 3px;
    font-weight: 600;
    text-anchor: middle;
    opacity: 0;
    animation: fadeIn 0.5s ease both;
    animation-delay: var(--delay);
  }
  
  .map-point:hover .point-label {
    opacity: 1;
  }
  
  .connection-line {
    stroke: rgba(79, 70, 229, 0.3);
    stroke-width: 0.3;
    stroke-dasharray: 100;
    stroke-dashoffset: 100;
    animation: drawLine 1s ease forwards;
    animation-delay: var(--delay);
  }
  
  .map-legend {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 10px;
  }
  
  .map-legend-item {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 0.8rem;
  }
  
  .country-name {
    flex: 1;
    opacity: 0.8;
  }
  
  .country-count {
    font-weight: 600;
  }
  
  /* === Activity Heatmap === */
  .heatmap-container {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .heatmap-hours {
    display: flex;
    justify-content: space-between;
    padding-left: 40px;
    font-size: 0.7rem;
    opacity: 0.5;
  }
  
  .heatmap-grid {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }
  
  .heatmap-row {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .heatmap-day {
    width: 30px;
    font-size: 0.7rem;
    opacity: 0.6;
    text-align: right;
  }
  
  .heatmap-cells {
    display: flex;
    gap: 2px;
    flex: 1;
  }
  
  .heatmap-cell {
    flex: 1;
    aspect-ratio: 1;
    background: rgba(79, 70, 229, calc(var(--intensity) * 0.8 + 0.1));
    border-radius: 3px;
    transition: all 0.2s;
    cursor: pointer;
    animation: scaleIn 0.3s ease both;
    animation-delay: var(--delay);
  }
  
  .heatmap-cell:hover {
    transform: scale(1.3);
    z-index: 10;
    box-shadow: 0 0 10px rgba(79, 70, 229, 0.5);
  }
  
  .heatmap-legend {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 10px;
    font-size: 0.7rem;
    opacity: 0.6;
  }
  
  .legend-gradient {
    width: 100px;
    height: 10px;
    background: linear-gradient(90deg, 
      rgba(79, 70, 229, 0.1), 
      rgba(79, 70, 229, 1));
    border-radius: 5px;
  }
  
  /* === Filters Section === */
  .filters-section {
    position: relative;
    z-index: 10;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 24px;
    background: var(--dash-glass);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 20px;
    border: 1px solid var(--dash-glass-border);
    margin-bottom: 30px;
    flex-wrap: wrap;
    gap: 20px;
    animation: fadeInUp 0.6s ease both;
    animation-delay: 0.3s;
  }
  
  .filters-left {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  
  .filter-buttons {
    display: flex;
    gap: 10px;
    flex-wrap: wrap;
  }
  
  .filter-btn-premium {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 10px 18px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--dash-glass-border);
    border-radius: 12px;
    color: inherit;
    font-size: 0.9rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
    position: relative;
    overflow: hidden;
  }
  
  .filter-btn-premium::before {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(135deg, var(--dash-primary), var(--dash-secondary));
    opacity: 0;
    transition: opacity 0.3s;
  }
  
  .filter-btn-premium:hover {
    transform: translateY(-2px);
    border-color: var(--dash-primary);
  }
  
  .filter-btn-premium.active {
    border-color: transparent;
  }
  
  .filter-btn-premium.active::before {
    opacity: 1;
  }
  
  .filter-btn-premium.active span {
    position: relative;
    z-index: 1;
  }
  
  .filter-icon {
    font-size: 1rem;
  }
  
  .filters-right {
    display: flex;
    align-items: center;
    gap: 16px;
  }
  
  .search-container {
    position: relative;
    display: flex;
    align-items: center;
  }
  
  .search-icon {
    position: absolute;
    left: 16px;
    width: 18px;
    height: 18px;
    opacity: 0.5;
  }
  
  .search-input-premium {
    width: 280px;
    padding: 12px 40px 12px 44px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--dash-glass-border);
    border-radius: 12px;
    color: inherit;
    font-size: 0.9rem;
    outline: none;
    transition: all 0.3s;
  }
  
  .search-input-premium::placeholder {
    opacity: 0.5;
  }
  
  .search-input-premium:focus {
    border-color: var(--dash-primary);
    box-shadow: 0 0 20px rgba(79, 70, 229, 0.2);
  }
  
  .search-clear {
    position: absolute;
    right: 12px;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.1);
    border: none;
    border-radius: 50%;
    color: inherit;
    font-size: 0.8rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.2s;
  }
  
  .search-clear:hover {
    background: rgba(255, 255, 255, 0.2);
  }
  
  .view-toggle {
    display: flex;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 10px;
    padding: 4px;
  }
  
  .view-btn {
    padding: 10px 14px;
    background: transparent;
    border: none;
    border-radius: 8px;
    color: inherit;
    cursor: pointer;
    transition: all 0.2s;
  }
  
  .view-btn:hover {
    background: rgba(255, 255, 255, 0.1);
  }
  
  .view-btn.active {
    background: var(--dash-primary);
  }
  
  .view-btn svg {
    width: 18px;
    height: 18px;
  }
  
  /* === Table Section === */
  .table-section {
    position: relative;
    z-index: 10;
    animation: fadeInUp 0.6s ease both;
    animation-delay: 0.4s;
  }
  
  .table-container {
    background: var(--dash-glass);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 20px;
    border: 1px solid var(--dash-glass-border);
    overflow: hidden;
  }
  
  .visitors-table-premium {
    width: 100%;
    border-collapse: collapse;
  }
  
  .visitors-table-premium thead tr {
    background: rgba(79, 70, 229, 0.1);
  }
  
  .visitors-table-premium th {
    padding: 18px 16px;
    text-align: left;
    font-size: 0.8rem;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
    opacity: 0.8;
    border-bottom: 1px solid var(--dash-glass-border);
  }
  
  .th-content {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .visitors-table-premium tbody .table-row {
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
    transition: all 0.3s;
    animation: fadeIn 0.3s ease both;
    animation-delay: var(--row-delay);
  }
  
  .visitors-table-premium tbody .table-row:hover {
    background: rgba(255, 255, 255, 0.08);
  }
  
  .visitors-table-premium td {
    padding: 16px;
    vertical-align: middle;
  }
  
  /* Status Indicator */
  .status-indicator {
    display: flex;
    align-items: center;
    gap: 8px;
  }
  
  .status-dot {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    animation: pulse 2s infinite;
  }
  
  .status-indicator.new .status-dot {
    background: var(--dash-success);
    box-shadow: 0 0 10px var(--dash-success);
  }
  
  .status-indicator.returning .status-dot {
    background: var(--dash-info);
    box-shadow: 0 0 10px var(--dash-info);
  }
  
  .status-text {
    font-size: 0.85rem;
    font-weight: 500;
  }
  
  /* Visitor ID */
  .visitor-id {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.85rem;
    padding: 6px 10px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 6px;
    opacity: 0.8;
  }
  
  /* Type Badge */
  .type-badge-premium {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    padding: 8px 14px;
    border-radius: 10px;
    font-size: 0.85rem;
    font-weight: 500;
    transition: all 0.2s;
  }
  
  .type-badge-premium.recruiter {
    background: rgba(77, 171, 247, 0.15);
    color: var(--dash-info);
  }
  
  .type-badge-premium.developer {
    background: rgba(255, 212, 59, 0.15);
    color: var(--dash-warning);
  }
  
  .type-badge-premium:hover {
    transform: scale(1.05);
  }
  
  /* Visits Count */
  .visits-count {
    display: flex;
    flex-direction: column;
    gap: 6px;
  }
  
  .visits-count strong {
    font-size: 1.1rem;
  }
  
  .visits-bar {
    width: 60px;
    height: 4px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 4px;
    overflow: hidden;
  }
  
  .visits-fill {
    height: 100%;
    background: linear-gradient(90deg, var(--dash-primary), var(--dash-secondary));
    border-radius: 4px;
    transition: width 0.5s ease;
  }
  
  /* Time Spent */
  .time-spent {
    font-family: 'JetBrains Mono', monospace;
    font-size: 0.9rem;
  }
  
  /* Source Badge */
  .source-badge {
    padding: 6px 12px;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 6px;
    font-size: 0.85rem;
  }
  
  /* Last Seen */
  .last-seen {
    display: flex;
    flex-direction: column;
    gap: 4px;
  }
  
  .time-ago {
    font-weight: 600;
    font-size: 0.9rem;
  }
  
  .full-date {
    font-size: 0.75rem;
    opacity: 0.5;
  }
  
  /* Lead Score */
  .lead-score-premium {
    display: flex;
    justify-content: center;
  }
  
  .score-ring {
    position: relative;
    width: 50px;
    height: 50px;
  }
  
  .score-ring svg {
    width: 100%;
    height: 100%;
    transform: rotate(-90deg);
  }
  
  .score-bg {
    fill: none;
    stroke: rgba(255, 255, 255, 0.1);
    stroke-width: 3;
  }
  
  .score-fill {
    fill: none;
    stroke: var(--dash-primary);
    stroke-width: 3;
    stroke-linecap: round;
    transition: stroke-dasharray 1s ease;
  }
  
  .score-value {
    position: absolute;
    inset: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.9rem;
    font-weight: 700;
  }
  
  /* Action Button */
  .action-btn {
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 10px 16px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid var(--dash-glass-border);
    border-radius: 10px;
    color: inherit;
    font-size: 0.85rem;
    cursor: pointer;
    transition: all 0.3s;
  }
  
  .action-btn:hover {
    background: var(--dash-primary);
    border-color: var(--dash-primary);
    transform: translateY(-2px);
  }
  
  .action-btn svg {
    width: 16px;
    height: 16px;
  }
  
  /* No Data */
  .no-data-row td {
    padding: 60px !important;
  }
  
  .no-data {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    opacity: 0.5;
  }
  
  .no-data svg {
    width: 50px;
    height: 50px;
  }
  
  /* === Footer === */
  .dashboard-footer {
    position: relative;
    z-index: 10;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 24px;
    background: var(--dash-glass);
    backdrop-filter: blur(20px);
    border-radius: 16px;
    border: 1px solid var(--dash-glass-border);
    margin-top: 30px;
    font-size: 0.9rem;
    animation: fadeInUp 0.6s ease both;
    animation-delay: 0.5s;
  }
  
  .footer-count {
    opacity: 0.8;
  }
  
  .footer-update {
    opacity: 0.6;
  }
  
  /* === Loading State === */
  .admin-loading {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 30px;
    background: linear-gradient(135deg, 
      rgba(79, 70, 229, 0.1) 0%, 
      rgba(124, 58, 237, 0.1) 50%, 
      rgba(236, 72, 153, 0.1) 100%);
  }
  
  .loader-container {
    position: relative;
    width: 80px;
    height: 80px;
  }
  
  .loader-ring {
    position: absolute;
    inset: 0;
    border: 3px solid transparent;
    border-radius: 50%;
    animation: loaderRotate 1.5s linear infinite;
  }
  
  .loader-ring:nth-child(1) {
    border-top-color: var(--dash-primary);
    animation-delay: 0s;
  }
  
  .loader-ring:nth-child(2) {
    inset: 10px;
    border-right-color: var(--dash-secondary);
    animation-delay: 0.15s;
    animation-direction: reverse;
  }
  
  .loader-ring:nth-child(3) {
    inset: 20px;
    border-bottom-color: var(--dash-accent);
    animation-delay: 0.3s;
  }
  
  .loader-core {
    position: absolute;
    inset: 30px;
    background: var(--dash-primary);
    border-radius: 50%;
    animation: loaderPulse 1s infinite;
  }
  
  .loading-text {
    font-size: 1.2rem;
    font-weight: 600;
    background: linear-gradient(135deg, var(--dash-primary), var(--dash-secondary));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .loading-progress {
    width: 200px;
    height: 4px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 4px;
    overflow: hidden;
  }
  
  .progress-bar {
    height: 100%;
    background: linear-gradient(90deg, var(--dash-primary), var(--dash-secondary));
    animation: progressBar 2s ease-in-out infinite;
  }
  
  /* === Unauthorized State === */
  .admin-unauthorized {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, 
      rgba(255, 107, 107, 0.1) 0%, 
      rgba(79, 70, 229, 0.1) 100%);
  }
  
  .unauthorized-content {
    text-align: center;
    padding: 60px;
    background: var(--dash-glass);
    backdrop-filter: blur(20px);
    border-radius: 30px;
    border: 1px solid var(--dash-glass-border);
    animation: scaleIn 0.5s ease;
  }
  
  .lock-icon {
    width: 80px;
    height: 80px;
    margin: 0 auto 30px;
    background: rgba(255, 107, 107, 0.2);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: pulse 2s infinite;
  }
  
  .lock-icon svg {
    width: 40px;
    height: 40px;
    color: var(--dash-danger);
  }
  
  .unauthorized-content h1 {
    font-size: 2.5rem;
    margin: 0 0 15px 0;
    background: linear-gradient(135deg, var(--dash-danger), var(--dash-primary));
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
  
  .unauthorized-content p {
    font-size: 1.1rem;
    opacity: 0.7;
    margin: 0 0 30px 0;
  }
  
  .back-btn {
    display: inline-flex;
    align-items: center;
    gap: 8px;
    padding: 14px 28px;
    background: linear-gradient(135deg, var(--dash-primary), var(--dash-secondary));
    color: #fff;
    text-decoration: none;
    border-radius: 14px;
    font-weight: 600;
    transition: all 0.3s;
  }
  
  .back-btn:hover {
    transform: translateY(-3px);
    box-shadow: 0 15px 40px rgba(79, 70, 229, 0.4);
  }
  
  /* === Responsive === */
  @media (max-width: 1200px) {
    .charts-section {
      grid-template-columns: 1fr 1fr;
    }
    
    .charts-section.secondary {
      grid-template-columns: 1fr;
    }
    
    .main-chart {
      grid-column: span 2;
    }
  }
  
  @media (max-width: 768px) {
    .admin-dashboard-premium {
      padding: 15px;
    }
    
    .header-content {
      flex-direction: column;
      align-items: stretch;
    }
    
    .header-left {
      justify-content: center;
      text-align: center;
    }
    
    .header-right {
      flex-direction: column;
    }
    
    .header-stats {
      justify-content: center;
    }
    
    .charts-section {
      grid-template-columns: 1fr;
    }
    
    .main-chart {
      grid-column: span 1;
    }
    
    .filters-section {
      flex-direction: column;
      align-items: stretch;
    }
    
    .filter-buttons {
      justify-content: center;
    }
    
    .search-input-premium {
      width: 100%;
    }
    
    .table-container {
      overflow-x: auto;
    }
    
    .visitors-table-premium {
      min-width: 900px;
    }
    
    .stats-section {
      grid-template-columns: repeat(2, 1fr);
    }
    
    .stat-value {
      font-size: 2rem;
    }
  }
  
  @media (max-width: 480px) {
    .stats-section {
      grid-template-columns: 1fr;
    }
    
    .filter-btn-premium span:last-child {
      display: none;
    }
    
    .header-text h1 {
      font-size: 1.5rem;
    }
  }
  
  /* === Reduced Motion === */
  @media (prefers-reduced-motion: reduce) {
    *,
    *::before,
    *::after {
      animation-duration: 0.01ms !important;
      animation-iteration-count: 1 !important;
      transition-duration: 0.01ms !important;
    }
  }